import {mapGetters, mapState} from 'vuex';
import appMenu from '../components/app-menu';
import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";
import appShareQrCode from '../../../components/page-component/app-share-qr-code-poster/app-share-qr-code-poster.vue';
export default {
    data() {
        return {
            tabList: [
                {id:-1, name: '全部'},
                {id:1, name: '进行中'},
                {id:0, name: '未开始'},
                {id:2, name: '已结束'}
            ],
            activeTab: -1,
            activeTabName: '全部',
            list: [],
            setting: {},
            widthRate: 1,
            id: 0,
            loading: false,
            more: false,
            page: 1,
            first: true,
            poster: '',
            longitude: '',
            latitude: '',
            middleman: {},
            middleman_id: '',
            animation: {},
            animationData: {}
        }
    },
    components: {
        "app-tab-nav": appTabNav,
        "app-menu": appMenu,
        appShareQrCode
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme'
        }),
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            bonusImg: state => state.mallConfig.__wxapp_img.bonus,
            userInfo: state => state.user.info,
        })
    },

    onShareTimeline() {
      // 分享朋友圈beta
      return this.$shareTimeline({
        title: this.setting.app_share_title ? this.setting.app_share_title : this.$children[0].navigationBarTitle,
        query: {} // 此处填写页面的参数
      });
    },

    onShow() {
        let that = this;
        that.animation = uni.createAnimation({
            duration: 400,
            timingFunction: 'linear',
        });
        if(that.first) {
            return false;
        }
        if(this.$storage.getStorageSync('middleman_info')) {
            let middleman = this.$storage.getStorageSync('middleman_info');
            if(middleman.id > 0) {
                that.middleman_id = middleman.user_id
            }
            that.getList();
        }else {
            that.getList();
        }
    },
    onLoad() { this.$commonLoad.onload();
        let that = this;
        uni.getSystemInfo({
            success: function (res) {
                let width = res.windowWidth * 2;
                that.widthRate = width / 750;
            }
        });

        wx.showShareMenu({
          menus: ['shareAppMessage', 'shareTimeline']
        })


        uni.getLocation({
            success: (e) => {
                uni.hideLoading();
                that.longitude = e.longitude;
                that.latitude = e.latitude;
                that.$showLoading({
                    type: 'global',
                    text: '加载中...'
                });
                that.getSetting();
            },
            fail: (e) => {
                uni.hideLoading();
                uni.showModal({
                    title: '提示',
                    content: '获取位置信息失败，需要授权获取您的位置信息',
                    showCancel: false,
                    confirmText: '打开授权',
                    success(e) {
                        if (e.confirm) {
                            uni.openSetting({});
                        }
                    }
                });
            },
        });

























































    },
    onReachBottom() {
        if(this.more) {
            this.page++;
            this.getMore();
            this.more = false;
        }
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: this.setting.app_share_title ? this.setting.app_share_title : this.$children[0].navigationBarTitle,
            imageUrl: this.setting.app_share_pic ? this.setting.app_share_pic : '',
            path: "/plugins/community/list/list"
        });
    },

    methods: {
        getMore() {
            let that = this;
            that.loading = true;
            let para = {
                longitude: that.longitude,
                latitude: that.latitude,
                page: that.page,
                status: that.activeTab
            };
            if(that.middleman_id > 0) {
                para.middleman_id = that.middleman_id
            }
            that.$request({
                url: that.$api.community.list,
                data: para,
            }).then(response=>{
                that.loading = false;
                that.first = false;
                that.$hideLoading();
                uni.hideLoading();
                if(response.code === 0) {
                    if (response.list.length === 20) {
                        that.more = true;
                    }else {
                        that.more = false
                    }
                    for(let index in response.list) {
                        if(response.list[index].goods_count == 0) {
                            response.list.splice(index,1)
                        }
                    }
                    that.list = that.list.concat(response.list);
                    for(let i in that.list) {
                        if(that.list[i].status_name === '已结束') {
                            that.list[i].statusText = '活动结束'
                        }else if(that.list[i].status_name === '进行中') {
                            that.list[i].statusText = that.list[i].end_at + '结束'
                        }else if(that.list[i].status_name === '未开始') {
                            that.list[i].statusText = that.list[i].start_at + '开始'
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.loading = false;
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        toDetail(item) {
            uni.navigateTo({
                url: '/plugins/community/activity/activity?id=' + item.id + '&title=' + item.title
            });
        },
        tabStatus(e,index) {
            let that = this;
            if(that.loading) {
                return false
            }
            that.list = [];
            that.page = 1;
            that.activeTab = e.id;
            that.activeTabName = e.name;
            let width = 88* +that.widthRate * index;
            that.animation.translate(width, 0).step();
            that.animationData = that.animation.export();
            // uni.showLoading({mask: true,
            //     title: '加载中...'
            // });
            that.getList();
        },
        getSetting() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.community.setting,
            }).then(response=>{
                if(response.code === 0) {
                    that.setting = response.data;
                    if(this.$storage.getStorageSync('middleman_info')) {
                        let middleman = this.$storage.getStorageSync('middleman_info');
                        if(middleman.id > 0) {
                            that.middleman_id = middleman.user_id
                        }
                        that.getList();
                    }else {
                        that.getList();
                    }
                }else {
                    that.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.loading = true;
            let para = {
                longitude: that.longitude,
                latitude: that.latitude,
                status: that.activeTab
            };
            if(that.middleman_id > 0) {
                para.middleman_id = that.middleman_id
            }
            that.$request({
                url: that.$api.community.list,
                data: para,
                method: 'post'
            }).then(response=>{
                that.loading = false;
                that.first = false;
                that.$hideLoading();
                uni.hideLoading();
                if(response.code === 0) {
                    if (response.list.length === 20) {
                        that.more = true;
                    }else {
                        that.more = false
                    }
                    for(let index in response.list) {
                        if(response.list[index].goods_count == 0) {
                            response.list.splice(index,1)
                        }
                    }
                    that.list = response.list;
                    for(let i in that.list) {
                        if(that.list[i].status_name === '已结束') {
                            that.list[i].statusText = '活动结束'
                        }else if(that.list[i].status_name === '进行中') {
                            that.list[i].statusText = that.list[i].end_at + '结束'
                        }else if(that.list[i].status_name === '未开始') {
                            that.list[i].statusText = that.list[i].start_at + '开始'
                        }
                    }
                    if(response.removeStorage) {
                        this.$storage.removeStorageSync('middleman_info');
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.loading = false;
                uni.hideLoading();
                that.$hideLoading();
            });
        }
    }
}
